






















import { Component, Vue } from 'vue-property-decorator';
import { Action } from 'vuex-class';

const namespace: string = 'app';

@Component({
  props: {
    isOpen: {
      type: Boolean,
      default: false,
      required: false
    },
    content: {
      type: String,
      required: true
    }
  }
})
export default class Popup extends Vue {
  @Action('togglePopup', { namespace }) private togglePopup: any;
}
